.main-header {
  width: 100%;
  height: 680px;
  position: relative;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.45);
}

.main-title {
  font-size: 40px;
  font-weight: 400;
  line-height: 53px;
  text-transform: uppercase;
  color: $c-text-pink;
  margin: 0 auto;
  text-align: center;
  max-width: 750px;
  padding: 0 0 33px;

  &__line {
    display: block;
  }
}

.video-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
}

.header-play-icon {
  width: 60px;
  margin: 0 auto;
  display: block;
  cursor: pointer;
}

.header-showreel {
  font-size: 16px;
  font-style: italic;
  text-align: center;
  color: $c-text-dark;
  padding-bottom: 95px;
}

.header-scroll-icon {
  display: block;
  width: 26px;
  margin: 0 auto;
}

.decor-line {
  width: 2px;
  height: 28px;
  background-color: $c-white;
  margin: 97px auto 30px;
}

@media screen and (max-width: 1199px) {
  .video-background {
    height: 100%;
    width: auto;
  }
}
